<template>
  <div class="invisible-box" @click="handleBoxClick"></div>
  <div
    class="notification jf"
    :class="notificationClass"
    :style="{ zIndex: notificationZIndex }"
  >
    <div class="message">{{ message }}</div>
    <div class="button-container">
      <button
        class="close-button jf-btn"
        @click="handleNotificationButtonClick"
      >
        確認
      </button>
    </div>
  </div>
  <div class="record">
    <div id="id"></div>
    <div id="score"></div>
    <div id="count"></div>
    <div id="level"></div>
    <div id="accelerationX"></div>
    <div id="accelerationY"></div>
    <div id="accelerationZ"></div>
  </div>
</template>

<script lang="js">
import { defineComponent } from "vue";
import { csmapi } from '/src/csmapi.js';

export default defineComponent({
  props: {
    uuid: {
      type: Number,
      required: true,
      default: 1,
    },
    name: {
      type: String,
      required: true,
      default: "1",
    },
  },
  data() {
    return {
      message: "取得加速度感測器權限",
      isActive: true,
      timeout: 0,
      acceleration: { x: 0, y: 0, z: 0, },
      cnt: 0,
      notshake: 1,
      level: 1,
      mylevelinterval: 0,
      mycntinterval: 0,
      lastClickTime: null,
      notificationZIndex: 9999,
    };
  },
  computed: {
    notificationClass() {
      return this.isActive ? "active" : "";
    },
  },
  mounted() {
    this.timeout = setTimeout(() => {
      this.hideNotification();
    }, 100000);
    this.mylevelinterval = setInterval(this.updatelevel, 500);
    this.setUpdateCntInterval();  // 使用新的方法來設定mycntinterval
  },
  beforeUnmount() {
    if (this.timeout) {
      clearTimeout(this.timeout);
    }
  },
  methods: {
    hideNotification() {
      this.isActive = false;
    },
    handleNotificationButtonClick() {
      this.requestDeviceMotionPermission()
        .then((permissionGranted) => {
          if (permissionGranted) {
            console.log("permissionGranted");
            this.hideNotification();
            this.pushScore();
            this.notificationZIndex = -1;
          } else {
            alert("未獲得加速度感測器權限");
          }
        })
        .catch(console.error);
    },
    requestDeviceMotionPermission() {
      return new Promise((resolve, reject) => {
        if (typeof DeviceMotionEvent.requestPermission === "function") {
          DeviceMotionEvent.requestPermission()
            .then((permissionState) => {
              if (permissionState === "granted") {
                window.addEventListener("devicemotion", this.handleDeviceMotion.bind(this));
                this.updateInterval = setInterval(this.updateScore, 100);
                resolve(true);
              } else {
                resolve(false);
              }
            })
            .catch(reject);
        } else {
          window.addEventListener("devicemotion", this.handleDeviceMotion);
          this.updateInterval = setInterval(this.updateScore, 100);
          resolve(true);
        }
      });
    },
    handleBoxClick() {
      const currentTime = Date.now();
      if (this.cnt < 15) {
        this.cnt += 1;
        this.notshake = 0;
        console.log("clicked! - " +  this.cnt);
      }
      this.lastClickTime = currentTime;
      this.$emit("update-level", this.level);
      this.$emit("update-cnt", this.cnt);
    },
    checkLastClickTime() {
      const currentTime = Date.now();
      if (this.lastClickTime && currentTime - this.lastClickTime >= 3000) {
        this.notclick = 1;
      } else {
        this.notclick = 0;
      }
    },
    handleDeviceMotion(event) {
        this.acceleration.x = event.accelerationIncludingGravity?.x || 0;
        this.acceleration.y = event.accelerationIncludingGravity?.y || 0;
        this.acceleration.z = event.accelerationIncludingGravity?.z || 0;

        var accelerationX = document.getElementById("accelerationX"),
            accelerationY = document.getElementById("accelerationY"),
            accelerationZ = document.getElementById("accelerationZ");

        accelerationX.innerHTML = this.acceleration?.x.toFixed(2)|| 0;
        accelerationY.innerHTML = this.acceleration?.y.toFixed(2)|| 0;
        accelerationZ.innerHTML = this.acceleration?.z.toFixed(2)|| 0;

        this.$emit("update-level", this.level);
        this.$emit("update-cnt", this.cnt);
    },
    updateScore() {
      const scoreElement = document.getElementById("score");
        const score = Math.sqrt(
            Math.pow(this.acceleration.x, 2) +
            Math.pow(this.acceleration.y, 2) +
            Math.pow(this.acceleration.z, 2)
        );

        scoreElement.innerHTML = score?.toFixed(2)|| 1;

        const cntElement = document.getElementById("count");
        // 這裡的 score 是指晃動程度
        if (score >= 15 && this.cnt < 15) {
            this.cnt += 1;
            this.notshake = 0;
        }
        cntElement.innerHTML = this.cnt?.toFixed(2)|| 0;
    },
    updatecnt() {
        if (this.notshake && this.cnt > 0 && this.notclick) {
          this.cnt -= 1;
        }
        this.notshake = 1;
        const cntElement = document.getElementById("count");//
        cntElement.innerHTML = this.cnt?.toFixed(2)|| 0;
    },
    updatelevel() {
      this.checkLastClickTime();
      const levelElement = document.getElementById("level");
      if (this.cnt >= 14 && this.level < 5) {
        this.level += 1;
          if (this.level == 5) {
          clearInterval(this.mylevelinterval);
          this.mylevelinterval = setInterval(this.updatelevel, 10000);
          } else {
          clearInterval(this.mylevelinterval);
          this.mylevelinterval = setInterval(this.updatelevel, 5000);
          }
          this.cnt = 0;
      } else if (this.cnt <= 1 && this.level > 1) {
          this.level -= 1;
          clearInterval(this.mylevelinterval);
          this.mylevelinterval = setInterval(this.updatelevel, 5000);
      }
      levelElement.innerHTML = this.level?.toFixed(2) || 1;
      this.setUpdateCntInterval();  // 等級變化時，重新設定mycntinterval
    },
    pushScore() {
      csmapi.set_endpoint("https://4.iottalk.tw");

      const mac_addr = "calltt";
      const idf_name = "callt";
      const data = [this.uuid, this.name, this.level];

      csmapi.push(mac_addr, idf_name, data, (data, error) => {
        if (error) {
          console.error("Failed to push score:", error);
          return;
        }
        setTimeout(this.pushScore, 5000);
      });
    },
    setUpdateCntInterval() {
      // 根據等級設定更新頻率，等級越高，頻率越低
      const interval = 500 * this.level;

      // 清除舊的計時器
      if (this.mycntinterval) {
        clearInterval(this.mycntinterval);
      }

      // 設定新的計時器
      this.mycntinterval = setInterval(this.updatecnt, interval);
    },

  },
});
</script>

